@font-face {
  font-family: "Montserrat";
  src: local("montserrat-medium"), url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-regular"), url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-semibold"), url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("montserrat-extrabold"), url("../fonts/montserrat-extrabold.woff2") format("woff2"), url("../fonts/montserrat-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.page {
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: #242222;
}

.page__img {
  width: 100%;
  height: 730px;
  position: absolute;
  z-index: -1;
  background: linear-gradient(0deg, rgba(50, 31, 51, 0.7) 0%, rgba(50, 31, 51, 0.7) 100%), url("../img/intro/intro-personal-trainer.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}
@media (min-width: 768px) {
  .page__img {
    height: 985px;
  }
}
@media (min-width: 1024px) {
  .page__img {
    height: 1050px;
  }
}
@supports not (background: linear-gradient(0deg, rgba(50, 31, 51, 0.7) 0%, rgba(50, 31, 51, 0.7) 100%), url("../img/intro/intro-personal-trainer.webp")) {
  .page__img {
    background: linear-gradient(0deg, rgba(50, 31, 51, 0.7) 0%, rgba(50, 31, 51, 0.7) 100%), url("../img/intro/intro-personal-trainer.jpg");
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page__img {
    background: linear-gradient(0deg, rgba(50, 31, 51, 0.7) 0%, rgba(50, 31, 51, 0.7) 100%), url("../img/intro/intro-personal-trainer@2x.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
  @supports not (background: linear-gradient(0deg, rgba(50, 31, 51, 0.7) 0%, rgba(50, 31, 51, 0.7) 100%), url("../img/intro/intro-personal-trainer@2x.webp")) {
    .page__img {
      background: linear-gradient(0deg, rgba(50, 31, 51, 0.7) 0%, rgba(50, 31, 51, 0.7) 100%), url("../img/intro/intro-personal-trainer@2x.jpg");
    }
  }
}

.container {
  width: 100%;
  max-width: 1235px;
  margin: 0 auto;
  padding: 0 9px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  flex: 0 0 auto;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 80px;
  max-width: 1020px;
  width: 100%;
  margin: 0 auto 33px;
  padding: 0 10px;
}
@media (min-width: 768px) {
  .header__wrapper {
    flex-direction: row;
  }
}
.header__wrapper a {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  padding-right: 9px;
}
@media (min-width: 768px) {
  .header__wrapper a {
    font-size: 14px;
    line-height: 17px;
    padding-right: 11px;
    letter-spacing: 0.5px;
  }
}

.header__contact {
  display: flex;
  margin-bottom: 7px;
}
@media (min-width: 768px) {
  .header__contact {
    margin-bottom: 0;
    margin-right: auto;
    margin-left: 15px;
  }
}
.header__contact svg {
  margin-right: 5px;
}
.header__contact a:hover, .header__contact a:focus-visible {
  transition: 0.3s ease-in-out;
  color: #c7e80a;
}
.header__contact a:active {
  opacity: 0.7;
}

.header__address {
  width: 220px;
}
@media (min-width: 768px) {
  .header__address {
    margin-top: 20px;
  }
  .header__address a {
    padding: 0;
  }
}
.header__address p {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  text-align: left;
  text-align: center;
  padding: 0 0 8px 0;
}
.header__address a {
  padding: 0;
}
.header__address svg {
  vertical-align: sub;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

.intro {
  position: relative;
  width: 100%;
}
.intro h1,
.intro p {
  margin: 0;
}
.intro h1 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 61px;
  line-height: 51px;
  font-weight: 800;
  font-style: normal;
  background: linear-gradient(0deg, #c7e80a 0%, #ffffff 100%);
  text-transform: uppercase;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  padding-bottom: 5px;
}
@media (min-width: 768px) {
  .intro h1 {
    text-align: left;
    font-size: 135px;
    line-height: 105px;
    padding-bottom: 18px;
  }
}
.intro p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  letter-spacing: -0.1px;
  text-align: center;
}
@media (min-width: 768px) {
  .intro p {
    font-size: 26px;
    line-height: 32px;
    font-weight: 500;
    text-align: left;
    padding-left: 9px;
    letter-spacing: 0px;
  }
}

.intro__wrapper {
  padding: 0 9px;
  position: relative;
  min-height: 565px;
}
@media (min-width: 768px) {
  .intro__wrapper {
    padding: 0;
    min-height: 845px;
  }
}

.intro__offer {
  margin-top: 33px;
}
@media (min-width: 768px) {
  .intro__offer {
    margin-top: 55px;
  }
}
.intro__offer p {
  color: #c7e80a;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0;
  padding-bottom: 2px;
  text-align: left;
}
@media (min-width: 768px) {
  .intro__offer p {
    font-size: 35px;
    line-height: 43px;
  }
}
.intro__offer span {
  border-bottom: 1px solid #c7e80a;
}

.intro__list {
  padding-left: 15px;
  margin-top: 15px;
}
@media (min-width: 768px) {
  .intro__list {
    width: 525px;
    padding-left: 28px;
    margin-top: 71px;
    margin-bottom: 46px;
  }
}

.intro__item {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  padding-bottom: 11px;
  letter-spacing: 0.15px;
}
@media (min-width: 768px) {
  .intro__item {
    font-size: 18px;
    line-height: 22px;
    font-weight: 600;
    padding-bottom: 31px;
    letter-spacing: 0;
  }
}
.intro__item span {
  border-bottom: 1px solid #ffffff;
}
.intro__item a {
  color: #c7e80a;
}

.about {
  border-radius: 50% 50% 0% 0%/7% 7% 85% 85%;
  background: linear-gradient(0deg, #242222 0, #432545 100%);
  min-height: 429px;
  position: relative;
  z-index: 2;
  margin-top: -30px;
}
@media (min-width: 768px) {
  .about {
    border-radius: 50% 50% 0% 0%/9% 9% 50% 50%;
    min-height: 640px;
  }
}
@media (min-width: 1024px) {
  .about {
    border-radius: 50% 50% 0% 0%/20% 20% 50% 50%;
  }
}

.about__desc {
  padding: 53px 0 5px;
  letter-spacing: 0.1px;
}
@media (min-width: 768px) {
  .about__desc {
    padding-top: 138px;
    width: 600px;
    text-align: center;
    margin: 0 auto;
  }
}
.about__desc h2,
.about__desc p {
  margin: 0;
}
.about__desc h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  font-style: normal;
  color: #c7e80a;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 32px;
}
@media (min-width: 768px) {
  .about__desc h2 {
    font-size: 30px;
    line-height: 37px;
    padding-bottom: 49px;
  }
}
.about__desc p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-style: normal;
  color: #ffffff;
}
@media (min-width: 768px) {
  .about__desc p {
    font-size: 20px;
    line-height: 26px;
  }
}
.about__desc span {
  color: #c7e80a;
}

.about__work-schedule {
  margin-bottom: 48px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .about__work-schedule {
    width: 280px;
    margin: 0 auto;
    text-align: center;
    padding-left: 0;
  }
}
.about__work-schedule p {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-style: normal;
  margin: 0;
  color: #c7e80a;
  padding-bottom: 3px;
}
@media (min-width: 768px) {
  .about__work-schedule p {
    font-size: 20px;
    line-height: 26px;
  }
}

.about__work-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.about__features-list {
  padding-left: 20px;
}
@media (min-width: 768px) {
  .about__features-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 28px;
    margin-bottom: 40px;
  }
}

.about__work-item,
.about__features-item {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-style: normal;
}
@media (min-width: 768px) {
  .about__work-item,
.about__features-item {
    font-size: 20px;
    line-height: 26px;
  }
}

.about__work-item {
  color: #ffffff;
}

.about__features-item {
  color: #c7e80a;
  padding-bottom: 5px;
}
@media (min-width: 768px) {
  .about__features-item {
    padding-bottom: 10px;
  }
}

.footer {
  flex: 0 0 auto;
  padding-bottom: 45px;
}

.footer__wrapper {
  min-height: 287px;
  background-color: #432545;
  border-radius: 20px;
  padding: 29px 33px 22px;
}
@media (min-width: 768px) {
  .footer__wrapper {
    min-height: 355px;
    max-width: 1045px;
    margin: 0 auto;
  }
}
.footer__wrapper a,
.footer__wrapper h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 12px;
  line-height: 15px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
}
@media (min-width: 768px) {
  .footer__wrapper a,
.footer__wrapper h2 {
    font-size: 20px;
    line-height: 24px;
  }
}
.footer__wrapper h2 {
  font-weight: 700;
  margin: 0;
  text-align: center;
  padding-bottom: 23px;
}
.footer__wrapper a {
  padding-bottom: 5px;
}
@media (min-width: 768px) {
  .footer__wrapper a:hover, .footer__wrapper a:focus-visible {
    transition: 0.3s ease-in-out;
    color: #c7e80a;
  }
  .footer__wrapper a:active {
    opacity: 0.7;
  }
}

@media (min-width: 768px) {
  .footer__inner {
    width: 660px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.footer__contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-top: 8px;
}
@media (min-width: 768px) {
  .footer__contacts-list {
    flex-flow: row wrap;
    justify-content: space-between;
    order: -1;
    margin-bottom: 10px;
  }
}

.footer__contacts-item {
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
}
.footer__contacts-item:last-child {
  padding-bottom: 0;
}
.footer__contacts-item p {
  margin: 0;
}

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-top: 18px;
}
.footer__social-list li {
  margin-right: 15px;
}
@media (min-width: 768px) {
  .footer__social-list li {
    margin-right: 45px;
  }
}
@media (min-width: 768px) {
  .footer__social-list svg {
    width: 56px;
    height: 56px;
  }
}

.button-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.button-link--map {
  min-height: 35px;
  border: 1px solid #c7e80a;
  background-color: transparent;
  color: #c7e80a;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  line-height: 12px;
  font-weight: 500;
  font-style: normal;
}
@media (min-width: 768px) {
  .button-link--map:hover, .button-link--map:focus-visible {
    background-color: #c7e80a;
    color: #242222;
    transition: 0.3s ease-in-out;
  }
  .button-link--map:active {
    opacity: 0.7;
  }
}
.button-link--intro {
  max-width: 366px;
  background-color: #c7e80a;
  min-height: 50px;
  color: #242222;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-style: normal;
  border-radius: 30px;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .button-link--intro {
    min-height: 71px;
    margin-left: 10px;
    font-size: 18px;
    line-height: 22px;
  }
  .button-link--intro:hover, .button-link--intro:focus-visible {
    border: 1px solid #c7e80a;
    background-color: transparent;
    transition: 0.3s ease-in-out;
    color: #ffffff;
  }
  .button-link--intro:active {
    opacity: 0.7;
  }
}